/**
 * src/components/ChatContainer.tsx
 * Main container for the chat interface with messages and input field
 * Used by: src/app/page.tsx
 */

"use client"

import React, { useState } from 'react';
import ChatInput from './ChatInput';

// ===== COMPONENT =====
export default function ChatContainer() {
  const [messages, setMessages] = useState<{content: string}[]>([]);

  const handleSendMessage = (content: string) => {
    setMessages((prev) => [...prev, {content}]);
    
    // For demo purposes, simulate a response
    setTimeout(() => {
      setMessages((prev) => [...prev, {content: `Echo: ${content}`}]);
    }, 1000);
  };

  return (
    <div className="flex flex-col h-screen p-4">
      <div className="flex-1 overflow-y-auto pb-4">
        {messages.length === 0 ? (
          <div className="h-full flex items-center justify-center">
            <p className="text-gray-500">Ask a question to get started</p>
          </div>
        ) : (
          <div className="space-y-4">
            {messages.map((message, index) => (
              <div key={index} className="max-w-3xl mx-auto">
                <p>{message.content}</p>
              </div>
            ))}
          </div>
        )}
      </div>
      <div>
        <ChatInput onSend={handleSendMessage} />
      </div>
    </div>
  );
} 